// import linkArea from '../../components/linkArea/linkArea.vue'
// import linkArea from '../../components/w-picker/w-picker.vue'
// import amap from '../../utils/amap-wx.js';
export default {
	data() {
		return {
			// amapPlugin: null,
			// key: 'ce334d5499fb668332c1a65513a54201',
			lotusAddressData: {
				visible: false,
				provinceName: '',
				cityName: '',
				townName: ''
			},
			region: '选择位置',
			form: {
				field06: '',
				field07: '健康',
				field08: '37以下',
				field09: '否',
				field10: '否',
				field11: '否',
				field12: '否',
				field13: '',
				field14: '',
				a: '', // 暂时获取页面数据
				b: '', // 暂时获取页面数据
				c: '' // 暂时获取页面数据
			},
			list1: [
				{
					value: '健康',
					name: '健康',
					checked: true
				},
				{
					value: '有发烧、咳嗽等症状',
					name: '有发烧、咳嗽等症状'
				},
				{
					value: '其他症状',
					name: '其他症状'
				}
			],
			list2: [
				{
					value: '无',
					name: '无'
				},
				{
					value: '有',
					name: '有'
				}
			],
			tempList: [
				{
					value: '37以下',
					name: '37以下'
				},
				{
					value: '37-38.5',
					name: '37-38.5'
				},
				{
					value: '38.5以上',
					name: '38.5以上'
				}
			],
			yesOrNo: [
				{
					value: '否',
					name: '否'
				},
				{
					value: '是',
					name: '是'
				}
			],
			vehicleList: [
				{
					value: '0',
					name: '飞机'
				},
				{
					value: '1',
					name: '火车'
				},
				{
					value: '2',
					name: '汽车'
				},
				{
					value: '3',
					name: '轮船'
				}
			]
		};
	},
	computed: {
		text1() {
			let textArr = ['飞机-航班号', '火车-车次', '汽车-起止地点', '轮船-起止地点'];
			return this.form.field14 ? textArr[this.form.field14] : '';
		},
		text2() {
			let textArr = ['飞机-起降地点', '火车-乘车区间', '汽车-是否司机'];
			return this.form.field14 < 3 ? textArr[this.form.field14] : '';
		},
		text3() {
			let textArr = ['飞机-座位号', '火车-车厢及座位号'];
			return this.form.field14 < 2 ? textArr[this.form.field14] : '';
		}
	},
	onLoad(e) {},
	mounted() {
		this.$getLocationByAmap().then((data)=>{
			let address = data[0].regeocodeData.addressComponent
			this.form.field06 = address.province + address.city + address.district;
			this.form.field05 = data[0].regeocodeData.addressComponent.city
		}).catch(()=>{
			
		});
		// this.amapPlugin = new amap.AMapWX({
		// 	key: this.key
		// });
		// this.judgeAuthorize();
	},
	methods: {
		handleClearData(){
			this.form.field14 = ''
		},
		validate() {
			let params = {
				...this.form
			}
			switch (this.form.field14) {
				case '0':
					{
						params.field15 = this.form.a;
						params.field16 = this.form.b;
						params.field17 = this.form.c;
						params.field14 = '飞机'
					}
					break;
				case '1':
					{
						this.form.field18 = this.form.a;
						this.form.field19 = this.form.b;
						this.form.field20 = this.form.c;
						params.field14 = '火车'
					}
					break;
				case '2':
					{
						this.form.field21 = this.form.a;
						this.form.field22 = this.form.b;
						params.field14 = '汽车'
					}
					break;
				case '3':
					{
						this.form.field23 = this.form.a;
						params.field14 = '轮船'
					}
					break;
			}
			params.field01 = uni.getStorageSync('userId'),
			params.field02 = uni.getStorageSync('idType')
			this.send(params);
		},
		send(params) {
			// 以上字段暂未确定
			// let params = {
			// 	...this.form,
			// 	field01: uni.getStorageSync('userId'),
			// 	field02: uni.getStorageSync('idType')
			// };
			// 去除多余属性
			delete params.a;
			delete params.b;
			delete params.c;
			this.$HTTP({
				url: '/healthForDay/save',
				params,
				successCallback: ({ data }) => {
					if (data.code == 0) {
						uni.showToast({
							title: '提交成功!'
						});
						setTimeout(() => {
							uni.navigateBack();
						}, 1500);
					} else {
						uni.showToast({
							title: data.msg,
							icon: 'none'
						});
					}
				}
			});
		},
		showChoose(refName) {
			this.$refs[refName].show();
		},
		handleCancelChoose(refName) {
			this.$refs[refName].hide();
		},
		handleChoose({ checkArr, checkValue, defaultVal, result }) {
			this.$refs.linkage.hide();
			this.form.field06 = result//checkValue.join(',');
			this.region = result;
		},
		handleChooseDate({ checkArr, checkValue, defaultVal, result }) {
			this.$refs.date.hide();
			this.form.field13 = checkArr[0].join('');
		},
		handleRadioChange(evt, name) {
			this.form[name] = evt.target.value;
		},
		
		judgeAuthorize() {
			uni.getSetting({
				success: info => {
					if (!info.authSetting['scope.userLocation']) {
						uni.authorize({
							scope: 'scope.userLocation',
							success: () => {
								// this.initPage()
								this.amapPlugin.getRegeo({
									success: data => {
										this.form.field06 = data[0].name;
										this.form.field05 = data[0].regeocodeData.addressComponent.city
									},
									fail:(err)=>{
										console.log(err,'err')
									}
								});
								uni.getLocation({
									success: res => {
										console.log(res, '地理位置');
									}
								});
							},
							fail: () => {
								uni.showToast({
									title:'请打开定位服务以便获取当前位置!',
									icon:'none',
									duration:3000
								})
							}
						});
					} else {
						// this.initPage()
						uni.getLocation({
							success: res => {
								this.amapPlugin.getRegeo({
									success: data => {
										this.form.field06 = data[0].name;
										this.form.field05 = data[0].regeocodeData.addressComponent.city
									},
									fail:(err)=>{
										console.log(err,'err')
									}
								});
							},
							fail: () => {
								uni.showToast({
									title:'请打开定位服务以便获取当前位置!',
									icon:'none',
									duration:3000
								})
							}
						});
					}
				},
				// complete: () => {
				// 	this.amapPlugin.getRegeo({
				// 		success: data => {
				// 			console.log(data, 'geo');
				// 			this.form.field06 = data[0].name;
				// 		}
				// 	});
				// }
			});
		}
	}
};
